import{ mapState,mapMutations} from 'vuex'
import indexList from "@/components/indexList.vue";

export default {
	data() {
		return {
			// audioPlaySrc:'../static/images/icon/img/title1.png',
			active:1,
			currentIndex:0,
			labelId:1,
			bannerList:[],
			tabs: [],
			listsTab:[],
			attentionList:[],
			token:uni.getStorageSync('token'),
			pageNum:1,//当前页数
			pageSize:10,//每页条数
			userId:'',
				
		};
	},
	onShareAppMessage(res) {
	      return {
	        title: '我在行动打卡，快来围观我吧',
	        path: '/pages/index/index'
	      }
	    },
	
	onLoad() {
	    this.indexData();
	},
	 computed: {  
	            ...mapState(['hasLogin'])  
	        },  
	methods:{
		...mapMutations(['logIn','logOut','IndexlogIn'])  ,
		goPage(url){
			if(!this.hasLogin){
				uni.navigateTo({
					url: '../login/login' 
				});
				return false;
			}
			uni.navigateTo({
				url
			});
		},
		//首页信息
		indexData:function(){
			var token=uni.getStorageSync('token');
			console.log(token)
			if(token!=''){
				this.IndexlogIn();
			}
			this.xd_request_post(this.xdServerUrls.xd_bannerList,{},true
			 ).then((res) => {
													   this.bannerList=res.obj
												   
												   }).catch(err => {						
												});
												this.xd_request_post(this.xdServerUrls.xd_label,{},false
													   ).then((res) => {
														   this.tabs=res.obj										   
													   }).catch(err => {									
												});
												this.getShowRecommend();
			
		},
		//围观
		lookerClick:function(list){
			if(!this.hasLogin){
				uni.navigateTo({
					url: '../login/login' 
				});
				return false;
			}
			this.userId=uni.getStorageSync('id');
			this.xd_request_post(this.xdServerUrls.xd_saveLooker,{
				
				pushId:list.id,
				lookUserId:this.userId,
			},false
			   ).then(res => {	
			
					   if(res.resultCode==0){
						   uni.showToast({
							title:'围观成功',
							 duration: 1000,
							 icon:'none',
						   })
					   }else if(res.resultCode==10015){
						   uni.showToast({
							title:'您已经围观了',
							 duration: 1000,
							 icon:'none',
						   })
					   }
					   
				
			})
		},
		//点赞
		loveClick:function(e,index){
			let list=e;
			if(!this.hasLogin){
				uni.navigateTo({
					url: '../login/login' 
				});
				return false;
			}
			this.xd_request_post(this.xdServerUrls.xd_saveGiveLikeByPush,{
				
				pushId:list.id,
				initiatorUserId:uni.getStorageSync('id'),
				giveLikeUserId:list.userId,
			},true
			   ).then(res => {	
					   console.log(res)
					   console.log(index)
					   if(!this.listsTab[index].currentUserGiveLike){								   
											this.listsTab[index].currentUserGiveLike=true;
											this.listsTab[index].giveLike++;
					   }else{
					   uni.showToast({
					   								title:'已经赞过了',
					   								 duration: 1000,
					   								 icon:'none',
					   })}
					   console.log(this.listsTab)
				   }).catch(err => {
					   if(err=='操作失败'){
						   uni.showToast({
							title:'已经赞过了',
							 duration: 1000,
							 icon:'none',
						   })
					   }
				
			})
			
			
		},
		// 最新
		showNew: function () {
			this.active = 0;	
			this.pageNum=1;
			this.listsTab=[];
			this.getShowNew();
		},
		getShowNew(){
			this.xd_request_post(this.xdServerUrls.xd_pushByCreateTimeList,
			{
				pageNum:1,
				pageSize:10,
			},
			true
				   ).then((res) => {
					  
					   this.listsTab=this.timeStamp(res);
					    this.pageNum=res.obj.nextPage; 
				   }).catch(err => {					
			                           });
			
		},
		// 推荐
		showRecommend : function(){
			this.active = 1;
			this.pageNum=1;
			this.listsTab=[];
			this.indexData();
			
		},
		getShowRecommend(){
			this.xd_request_post(this.xdServerUrls.xd_pushByHighGradeList,
			{
				token:uni.getStorageSync('token') ,
				pageNum:1,
				pageSize:10,
			},
			true
				   ).then((res) => {
					   this.listsTab=this.timeStamp(res);
					   this.pageNum=res.obj.nextPage;
				   }).catch(err => {											
			                           });
			
		},
		timeStamp(res){
			let dataList=res.obj.list;
			for(var i=0;i <res.obj.list.length;i++){
			   var  time=this.xdUniUtils.xd_timestampToTime(res.obj.list[i].createTime);
				dataList[i].createTime=time;
				dataList[i].challengeRmb=Math.trunc(dataList[i].challengeRmb/100);
				
			}
			return dataList;
		},
		// 关注
		showFollow : function(){
			this.active = 2;
			this.pageNum=1;
			this.getShowFollow();
		},
		getShowFollow(){
			if(!this.hasLogin){
				uni.navigateTo({
					url: '../login/login' 
				});
				return false;
			}
			this.xd_request_post(this.xdServerUrls.xd_getAttentionList,
			{
				userId:uni.getStorageSync('id'),
				pageNum:1,
				pageSize:10,
			},
			true
				   ).then((res) => {
					   this.attentionList=res.obj.list;
					   this.pageNum=res.obj.nextPage;
				   }).catch(err => {											
			                           });
			
			
		},
		//标签获取列表
		getPushByLabel(){
			this.xd_request_post(this.xdServerUrls.xd_pushByLabel,
			{
				token:uni.getStorageSync('token') ,
				pageNum:1,
				pageSize:10,
				labelId:this.labelId,
			},
			true
				   ).then((res) => {
					   this.listsTab=this.timeStamp(res);
					   this.pageNum=res.obj.nextPage==undefined? 1:res.obj.nextPage;
				   }).catch(err => {											
			                           });
			
		},
		
		// 推荐内容切换
		navChange: function (e) {
			console.log(e)
			this.pageNum=1;
			this.listsTab=[];
			this.active=3;
			this.currentIndex = e.currentTarget.dataset.index;
			this.labelId=e.currentTarget.dataset.id;
			this.getPushByLabel();
			
		},
		getNewList(){
		},
		 getReachList(){
			 var data={};
			 let that=this;
			if(that.pageNum==0){
				uni.showLoading(
				{
					title: '没有更多数据了'
				})
				setTimeout(function () {
				    uni.hideLoading();
				}, 1000);
				
				return false
			}
			uni.showLoading(
			{
				title: '加载中..',
			})
			switch(that.active){
				case 0:
				that.xd_request_post(that.xdServerUrls.xd_pushByCreateTimeList,
				{
					
					pageNum:that.pageNum,
					pageSize:that.pageSize,
				},
				true
					   ).then(res=>{
						 that.pageNum=res.obj.nextPage;
						 data =that.timeStamp(res);
						that.listsTab = that.listsTab.concat(data);					
						setTimeout(function () {
						    uni.hideLoading()
						}, 1000);	
					})
				
				break;
				case 1:
				that.xd_request_post(that.xdServerUrls.xd_pushByHighGradeList,
				{
					pageNum:that.pageNum,
					pageSize:that.pageSize,
				},
				true
					   ).then(res=>{
						   that.pageNum=res.obj.nextPage;
								   
							  data=that.timeStamp(res);
						that.listsTab = that.listsTab.concat(data);					
						setTimeout(function () {
						    uni.hideLoading()
						}, 1000);							
					})
				break;
				case 2:
				if(!that.hasLogin){
					uni.navigateTo({
						url: '../login/login' 
					});
					return false;
				}
				that.xd_request_post(that.xdServerUrls.xd_getAttentionList,
				{
					userId:uni.getStorageSync('id'),
					pageNum:that.pageNum,
					pageSize:that.pageSize,
				},
				true
					   ).then(res=>{
						that.pageNum=res.obj.nextPage;						
						that.attentionList = that.attentionList.concat(res.obj.list);					
						setTimeout(function () {
						    uni.hideLoading()
						}, 1000);
						
					})
				
				break;
				case 3:
				that.xd_request_post(that.xdServerUrls.xd_pushByLabel,
				{
					pageNum:that.pageNum,
					pageSize:that.pageSize,
					labelId:this.labelId,
				},
				true
					   ).then(res=>{
						   that.pageNum=res.obj.nextPage;
								   
							  data=that.timeStamp(res);
						that.listsTab = that.listsTab.concat(data);					
						setTimeout(function () {
						    uni.hideLoading()
						}, 1000);							
					})
				break;
			}
		},
		// 跳转外部链接
		tonewurl(e) {
			let url=e;
			// 判断链接是否为空
			if (url == null) {
				return false;
			}
			// 判断链接是否为https
			let notS = url.split(':')[0];
			let a = notS.indexOf('s') > -1;
			if (a == false) {
				uni.navigateTo({
					url: url
				});
				return false;
			}		
			//  链接拼接编码网址（同时用模板字符串放置所需要的数据）
			// url = encodeURIComponent(url + `?typefrom=${typefrom}&utoken=${utoken}&unionid=${unionid}&shopid=${shopId}`);
			url = encodeURIComponent(url);
			uni.navigateTo({
				url: '../web/webShow?url=' + url
			});
		},      
		
	},
	onShow() {
		this.active=1;
		this.indexData();
	},
	// 下拉刷新
	onPullDownRefresh() {
		switch(this.active){
			case 0:
			this.getShowNew(),
			this.pageNum=1,
			uni.stopPullDownRefresh();
			break;
			case 1:
			this.indexData(),
			this.pageNum=1,
			uni.stopPullDownRefresh();
			
			case 2:
			this.getShowFollow(),
			this.pageNum=1,
			uni.stopPullDownRefresh();
			break;
			case 3:
			this.indexData(),
			this.pageNum=1,
			uni.stopPullDownRefresh();
			this.currentIndex=0;
			break;
		}
	},
	// 上拉加载
	onReachBottom() {
		this.getReachList()
	},
	components:{
		indexList
	}
	
	
}

